.backDrop {
  position: fixed;
  z-index: 10;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(3px);
}

.modalContainer {
  width: 500px;
  min-height: 300px;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 3px 3px 10px #000000;
}